import React from 'react'
import { graphql } from 'gatsby'
import Layout from '../components/layout'
import Entry from '../components/entry'
import Typography from '@material-ui/core/Typography'
import withRoot from '../withRoot'

const IndexPage = ({ data }) => {
  let entriesData = data.allMarkdownRemark.edges.map(entry => {
    return {
      date: entry.node.frontmatter.date,
      course: entry.node.frontmatter.course
        ? entry.node.frontmatter.course
        : '',
      courseUrl: entry.node.frontmatter.courseUrl
        ? entry.node.frontmatter.courseUrl
        : '',
      title: entry.node.frontmatter.title,
      text: entry.node.excerpt,
    }
  })

  let entries = entriesData.map((entry, index) => (
    <Entry
      key={index}
      date={entry.date}
      title={entry.title}
      content={
        <Typography
          style={{ marginBottom: 10 }}
          variant="body1"
          color="inherit"
        >
          {entry.text}
        </Typography>
      }
      course={entry.course}
      courseUrl={entry.courseUrl}
    />
  ))

  return <Layout title="Index">{entries}</Layout>
}

export default withRoot(IndexPage)

export const query = graphql`
  query {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          frontmatter {
            title
            date
          }
          excerpt
        }
      }
    }
  }
`
